<template>
    <div id="app">
        <h1>WqyAshley's TODOList</h1>
        <div class="input-div">
            <el-input
                    @change="addTodo"
                    class="input"
                    clearable
                    placeholder="请输入代办事项"
                    v-model="inputValue"></el-input>
        </div>
        <ul>
            <li v-for="(item,i) in this.$store.state.todoList" :key="i">
                <el-button type="text" @click="delTodo(item)">{{item}}</el-button>
            </li>
        </ul>
    </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      inputValue: ''
    }
  },
  methods: {
    addTodo () {
      if (!this.inputValue.length == 0) {
        console.log(this.inputValue)
        this.$store.commit('add', this.inputValue)
      } else {
        console.log(typeof this.inputValue.length)
      }
    },
    delTodo (todo) {
      console.log(todo)
      this.$store.commit('del', todo)
    }
  }
}
</script>

<style>
    #app {
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        margin-top: 60px;
    }

    .input-div {
        display: inline-block;
        width: 30%;
    }
</style>



