import Vue from 'vue'
import Vuex from 'vuex'
import createVuexAlong from 'vuex-along'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    todoList: []
  },
  mutations: {
    add (state, todo) {
      state.todoList.push(todo)
    },
    del (state, todo) {
      state.todoList.splice(todo, 1)
    }
  },
  actions: {},
  modules: {},
  plugins: [createVuexAlong()]
})



